.footer {
    box-shadow: 0 -5px 40px #00000029;
    padding: 20px 0;
}

.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer-info {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.footer-copy {
    font-size: 0.69rem;
    line-height: 1.5rem;
    color: var(--color-grey);
    margin-top: 6px;
}

.footer-icons a:first-of-type {
    margin-right: 20px;
}